// Global Styles

*,
*::before,
*::after {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
    min-height: 100vh;
    @extend .body-light;
    color: $dark;
    background-color: $white;
}

a {
    text-decoration: none;
    cursor: pointer;
}

ul,
ol {
    list-style: none;
}

// Utilities
.flex-c {
    display: flex;
    justify-content: center;
    align-items: center;
}

.box-shadow {
    box-shadow: 0px 2px 4px $box-shadow;
}

.projects-bg {
    background-color: $projects;
}

.projects-bg-light {
    background-color: $projects;
}

.projects-btn {
    color: $dark;
    @extend .h4;
    font-family: $regular;
    font-size: 20px;
    position: relative;

    &::before {
        content: '';
        background-color: $projects;
        position: absolute;
        left: 0;
        bottom: -4px;
        width: 50%;
        height: 4px;
        z-index: -1;
        transition: all .3s ease-in-out;
    }

    &:hover::before {
        width: 100%;
    }
}

.primary-btn {
    background-color: $primary-color;
    color: $light-bg;
}

.dark-color {
    color: $dark;
}

.chip-btn {
    color: $dark;
    border-radius: 50px;
    padding: 4px 16px;
    min-width: 120px;
    @extend .chips;
    cursor: pointer;
    background-color: $dark;
    color: $white;
    transition: all .3s ease-in-out;

    &--active {
        @extend .chip-btn;
        color: $dark;
        background-color: $projects;
    }

    @include tablet {
        &:hover {
            box-shadow: 0px 4px 4px 2px $box-shadow;
            transform: scale(1.05);
            background-color: $projects;
            color: $dark;
        }
    }
}

.home {
    min-height: 100vh;
}